
"use client";

import { zodResolver } from "@hookform/resolvers/zod";
import { useForm, Controller } from "react-hook-form";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import { Checkbox } from "@/components/ui/checkbox";
import { Slider } from "@/components/ui/slider";
import { db, storage } from '@/lib/firebase';
import { collection, addDoc, serverTimestamp, updateDoc } from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import { useToast } from "@/hooks/use-toast";
import { Loader2 } from "lucide-react";
import { 
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogClose
} from "@/components/ui/dialog";

const fileSchema = z.any()
  .refine(files => files?.length == 1, "Este archivo es requerido.")
  .refine(files => files?.[0]?.size <= 5_000_000, `El tamaño máximo del archivo es 5 MB.`)
  .refine(
    files => ["image/jpeg", "image/png", "image/webp"].includes(files?.[0]?.type),
    "Solo se permiten formatos .jpg, .png, and .webp."
  );

const castingFormSchema = z.object({
  fullName: z.string().min(3, "El nombre debe tener al menos 3 caracteres.").max(80, "El nombre no debe exceder los 80 caracteres."),
  age: z.number({ required_error: "La edad es requerida." }).min(18, "Debes ser mayor de 18 años.").max(55, "La edad máxima es 55 años."),
  nationality: z.string().max(60).optional(),
  city: z.string().min(1, "La ciudad es requerida.").max(60),
  phone: z.string().regex(/^\+?[0-9\s\-]{7,20}$/, "Número de teléfono inválido."),
  email: z.string().email("Email inválido."),
  socialLinks: z.string().url("Debe ser una URL válida.").optional().or(z.literal('')),
  heightCm: z.number({ required_error: "La estatura es requerida." }).min(140, "La estatura mínima es 140 cm.").max(210, "La estatura máxima es 210 cm."),
  clothingSize: z.enum(["XS", "S", "M", "L", "XL"], { required_error: "La talla de ropa es requerida." }),
  shoeSize: z.number().min(30).max(48).optional(),
  eyesColor: z.enum(["Café", "Negro", "Verde", "Azul", "Avellana", "Otro"]).optional(),
  hairColor: z.enum(["Negro", "Castaño", "Rubio", "Pelirrojo", "Teñido", "Rapado", "Otro"]).optional(),
  hasExperience: z.enum(["Sí", "No"], { required_error: "Debes seleccionar una opción." }),
  experienceDesc: z.string().max(800).optional(),
  danceSkills: z.array(z.string()).optional(),
  actingExperience: z.enum(["Ninguna", "Básica", "Intermedia", "Avanzada"]).optional(),
  cameraConfidence: z.number().min(1).max(5),
  musicStyles: z.array(z.string()).optional(),
  rehearsalAvailability: z.boolean().refine(val => val === true, "Debes confirmar disponibilidad para ensayos."),
  canTravel: z.enum(["Sí", "No"], { required_error: "Debes seleccionar una opción." }),
  medicalRestrictions: z.string().max(500).optional(),
  headshot: fileSchema,
  fullBodyPhoto: fileSchema,
  additionalPhotos: z.any().optional().refine(files => !files || files.length <= 5, "Puedes subir hasta 5 fotos adicionales."),
  reelOrDanceLink: z.string().url("URL inválida.").optional().or(z.literal('')),
  socialConsent: z.boolean().optional(),
  privacyConsent: z.literal(true, { errorMap: () => ({ message: "Debes aceptar la política de privacidad." }) }),
  over18: z.literal(true, { errorMap: () => ({ message: "Debes confirmar que eres mayor de 18 años." }) }),
}).refine(data => {
    if (data.hasExperience === "Sí") {
        return !!data.experienceDesc && data.experienceDesc.length > 0;
    }
    return true;
}, {
    message: "La descripción de tu experiencia es requerida.",
    path: ["experienceDesc"],
});


type CastingFormValues = z.infer<typeof castingFormSchema>;

export function CastingForm() {
  const { toast } = useToast();
  const { register, handleSubmit, formState: { errors, isSubmitting }, watch, control, reset } = useForm<CastingFormValues>({
    resolver: zodResolver(castingFormSchema),
    mode: "onBlur",
    defaultValues: {
      cameraConfidence: 3,
      danceSkills: [],
      musicStyles: [],
      hasExperience: undefined,
      socialConsent: false,
      rehearsalAvailability: false,
    },
  });

  const hasExperience = watch("hasExperience");
  const danceSkills = watch("danceSkills") || [];
  
  const uploadFile = async (file: File, path: string): Promise<string> => {
    const storageRef = ref(storage, path);
    await uploadBytes(storageRef, file);
    return getDownloadURL(storageRef);
  };
  
  async function onSubmit(data: CastingFormValues) {
    try {
      // 1. Create document in Firestore to get an ID
      const docData = {
        createdAt: serverTimestamp(),
        status: "received",
        project: "Casting General",
        fullName: data.fullName,
        age: data.age,
        nationality: data.nationality || null,
        city: data.city,
        phone: data.phone,
        email: data.email,
        socialLinks: data.socialLinks || null,
        heightCm: data.heightCm,
        clothingSize: data.clothingSize,
        shoeSize: data.shoeSize || null,
        eyesColor: data.eyesColor || null,
        hairColor: data.hairColor || null,
        hasExperience: data.hasExperience === "Sí",
        experienceDesc: data.experienceDesc || null,
        danceSkills: data.danceSkills || [],
        actingExperience: data.actingExperience || null,
        cameraConfidence: data.cameraConfidence,
        musicStyles: data.musicStyles || [],
        rehearsalAvailability: data.rehearsalAvailability,
        canTravel: data.canTravel === "Sí",
        medicalRestrictions: data.medicalRestrictions || null,
        assets: {
          headshot: "",
          fullBodyPhoto: "",
          additionalPhotos: [],
          reelOrDanceLink: data.reelOrDanceLink || null
        },
        consents: {
          social: data.socialConsent || false,
          privacy: data.privacyConsent,
          over18: data.over18,
        }
      };

      const docRef = await addDoc(collection(db, "cast_applications"), docData);
      const docId = docRef.id;

      // 2. Upload files to Storage
      const headshotUrl = await uploadFile(data.headshot[0], `casting_uploads/${docId}/headshot_${docId}`);
      const fullBodyPhotoUrl = await uploadFile(data.fullBodyPhoto[0], `casting_uploads/${docId}/fullbody_${docId}`);
      
      let additionalPhotosUrls: string[] = [];
      if (data.additionalPhotos && data.additionalPhotos.length > 0) {
        additionalPhotosUrls = await Promise.all(
          Array.from(data.additionalPhotos).map((file, index) => 
            uploadFile(file as File, `casting_uploads/${docId}/extras/img_${index}`)
          )
        );
      }

      // 3. Update document with file URLs
      await updateDoc(docRef, {
        'assets.headshot': headshotUrl,
        'assets.fullBodyPhoto': fullBodyPhotoUrl,
        'assets.additionalPhotos': additionalPhotosUrls,
      });

      toast({
        title: "¡Postulación Enviada!",
        description: "Hemos recibido tu información correctamente. ¡Mucha suerte!",
      });
      reset();
      
      // TODO: Redirect to a success page
      
    } catch (error) {
      console.error("Error submitting form:", error);
      toast({
        variant: "destructive",
        title: "Error al enviar",
        description: "Hubo un problema al procesar tu postulación. Por favor, inténtalo de nuevo.",
      });
    }
  }

  const danceSkillOptions = ["Urbano", "Reggaetón", "Comercial", "Contemporáneo", "Salsa/Bachata", "Hip-Hop", "Jazz", "Heels", "Otro"];
  const musicStyleOptions = ["Pop", "Urbano/Reggaetón", "Electrónica", "Rock", "Alternativa/Indie", "Latina", "Otro"];
  const eyesColorOptions = ["Café", "Negro", "Verde", "Azul", "Avellana", "Otro"];
  const hairColorOptions = ["Negro", "Castaño", "Rubio", "Pelirrojo", "Teñido", "Rapado", "Otro"];

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="space-y-12">
      {/* Sección 1: Información Básica */}
      <div className="space-y-6 border-b border-border pb-12">
        <h2 className="text-xl font-headline font-semibold text-foreground">Información Básica</h2>
        <div className="grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            <div className="sm:col-span-3">
                <Label htmlFor="fullName">Nombre completo</Label>
                <Input id="fullName" {...register("fullName")} aria-invalid={!!errors.fullName} aria-describedby="fullName-error" />
                {errors.fullName && <p id="fullName-error" className="text-destructive text-sm mt-1">{errors.fullName.message}</p>}
            </div>
            <div className="sm:col-span-3">
                <Label htmlFor="age">Edad</Label>
                <Input id="age" type="number" {...register("age", { valueAsNumber: true })} aria-invalid={!!errors.age} aria-describedby="age-error" />
                {errors.age && <p id="age-error" className="text-destructive text-sm mt-1">{errors.age.message}</p>}
            </div>
            <div className="sm:col-span-3">
                <Label htmlFor="nationality">Nacionalidad</Label>
                <Input id="nationality" {...register("nationality")} />
            </div>
            <div className="sm:col-span-3">
                <Label htmlFor="city">Ciudad y país de residencia</Label>
                <Input id="city" {...register("city")} aria-invalid={!!errors.city} aria-describedby="city-error" />
                {errors.city && <p id="city-error" className="text-destructive text-sm mt-1">{errors.city.message}</p>}
            </div>
             <div className="sm:col-span-3">
                <Label htmlFor="phone">Teléfono</Label>
                <Input id="phone" type="tel" {...register("phone")} placeholder="+506 8888-8888" aria-invalid={!!errors.phone} aria-describedby="phone-error" />
                {errors.phone && <p id="phone-error" className="text-destructive text-sm mt-1">{errors.phone.message}</p>}
            </div>
             <div className="sm:col-span-3">
                <Label htmlFor="email">Email</Label>
                <Input id="email" type="email" {...register("email")} aria-invalid={!!errors.email} aria-describedby="email-error" />
                {errors.email && <p id="email-error" className="text-destructive text-sm mt-1">{errors.email.message}</p>}
            </div>
            <div className="col-span-full">
                <Label htmlFor="socialLinks">Redes Sociales (Instagram, TikTok, etc.)</Label>
                <Textarea id="socialLinks" {...register("socialLinks")} className="mt-1" placeholder="https://instagram.com/tu_usuario" aria-describedby="socialLinks-error" />
                {errors.socialLinks && <p id="socialLinks-error" className="text-destructive text-sm mt-1">{errors.socialLinks.message}</p>}
            </div>
        </div>
      </div>

      <div className="space-y-6 border-b border-border pb-12">
        <h2 className="text-xl font-headline font-semibold text-foreground">Aspecto Físico</h2>
        <div className="grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            <div className="sm:col-span-2">
                <Label htmlFor="heightCm">Estatura (cm)</Label>
                <Input id="heightCm" type="number" {...register("heightCm", { valueAsNumber: true })} aria-invalid={!!errors.heightCm} aria-describedby="heightCm-error" />
                {errors.heightCm && <p id="heightCm-error" className="text-destructive text-sm mt-1">{errors.heightCm.message}</p>}
            </div>
            <div className="sm:col-span-2">
                <Label htmlFor="clothingSize">Talla de Ropa</Label>
                <Controller
                    name="clothingSize"
                    control={control}
                    render={({ field }) => (
                        <Select onValueChange={field.onChange} defaultValue={field.value} aria-required="true">
                        <SelectTrigger id="clothingSize" aria-invalid={!!errors.clothingSize}><SelectValue placeholder="Selecciona..." /></SelectTrigger>
                        <SelectContent>
                            <SelectItem value="XS">XS</SelectItem>
                            <SelectItem value="S">S</SelectItem>
                            <SelectItem value="M">M</SelectItem>
                            <SelectItem value="L">L</SelectItem>
                            <SelectItem value="XL">XL</SelectItem>
                        </SelectContent>
                        </Select>
                    )}
                />
                {errors.clothingSize && <p className="text-destructive text-sm mt-1">{errors.clothingSize.message}</p>}
            </div>
             <div className="sm:col-span-2">
                <Label htmlFor="shoeSize">Talla de calzado (EUR)</Label>
                <Input id="shoeSize" type="number" {...register("shoeSize", { setValueAs: (v) => v === "" ? undefined : parseInt(v, 10) })} />
                {errors.shoeSize && <p className="text-destructive text-sm mt-1">{errors.shoeSize.message}</p>}
            </div>
            <div className="sm:col-span-3">
                <Label htmlFor="eyesColor">Color de ojos</Label>
                 <Controller
                    name="eyesColor"
                    control={control}
                    render={({ field }) => (
                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                        <SelectTrigger id="eyesColor"><SelectValue placeholder="Selecciona..." /></SelectTrigger>
                        <SelectContent>
                            {eyesColorOptions.map(color => <SelectItem key={color} value={color}>{color}</SelectItem>)}
                        </SelectContent>
                        </Select>
                    )}
                />
            </div>
            <div className="sm:col-span-3">
                <Label htmlFor="hairColor">Color de cabello</Label>
                 <Controller
                    name="hairColor"
                    control={control}
                    render={({ field }) => (
                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                        <SelectTrigger id="hairColor"><SelectValue placeholder="Selecciona..." /></SelectTrigger>
                        <SelectContent>
                            {hairColorOptions.map(color => <SelectItem key={color} value={color}>{color}</SelectItem>)}
                        </SelectContent>
                        </Select>
                    )}
                />
            </div>
        </div>
      </div>
      
      <div className="space-y-6 border-b border-border pb-12">
          <h2 className="text-xl font-headline font-semibold text-foreground">Experiencia y Habilidades</h2>
          <div className="space-y-8">
              <fieldset>
                  <legend className="text-sm font-medium text-foreground">¿Has trabajado en videos musicales, comerciales o sesiones de fotos?</legend>
                   <Controller
                      name="hasExperience"
                      control={control}
                      render={({ field }) => (
                        <RadioGroup onValueChange={field.onChange} defaultValue={field.value} className="mt-2 flex gap-4" aria-required="true">
                            <div className="flex items-center space-x-2">
                                <RadioGroupItem value="Sí" id="exp-yes" />
                                <Label htmlFor="exp-yes" className="font-normal">Sí</Label>
                            </div>
                            <div className="flex items-center space-x-2">
                                <RadioGroupItem value="No" id="exp-no" />
                                <Label htmlFor="exp-no" className="font-normal">No</Label>
                            </div>
                        </RadioGroup>
                      )}
                    />
                  {errors.hasExperience && <p className="text-destructive text-sm mt-1">{errors.hasExperience.message}</p>}
              </fieldset>

              {hasExperience === "Sí" && (
                  <div>
                      <Label htmlFor="experienceDesc">Describe tu experiencia</Label>
                      <Textarea id="experienceDesc" {...register("experienceDesc")} className="mt-1" rows={4} placeholder="Marca/Artista, año, rol, enlace si existe" aria-required="true" aria-invalid={!!errors.experienceDesc} aria-describedby="experienceDesc-error" />
                      {errors.experienceDesc && <p id="experienceDesc-error" className="text-destructive text-sm mt-1">{errors.experienceDesc.message}</p>}
                  </div>
              )}

              <div>
                  <Label>Habilidades de Baile</Label>
                   <div className="mt-2 grid grid-cols-2 sm:grid-cols-3 gap-4">
                        {danceSkillOptions.map(skill => (
                           <Controller
                                key={skill}
                                name="danceSkills"
                                control={control}
                                render={({ field }) => (
                                    <div className="flex items-center gap-2">
                                        <Checkbox 
                                            id={`skill-${skill}`} 
                                            checked={field.value?.includes(skill)}
                                            onCheckedChange={(checked) => {
                                                return checked
                                                    ? field.onChange([...(field.value || []), skill])
                                                    : field.onChange(field.value?.filter(v => v !== skill))
                                            }}
                                        />
                                        <Label htmlFor={`skill-${skill}`} className="font-normal">{skill}</Label>
                                    </div>
                                )}
                            />
                        ))}
                   </div>
              </div>
              
                <div>
                    <Label htmlFor="actingExperience">Experiencia en actuación</Label>
                    <Controller
                        name="actingExperience"
                        control={control}
                        render={({ field }) => (
                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                            <SelectTrigger id="actingExperience" className="mt-1"><SelectValue placeholder="Selecciona..." /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="Ninguna">Ninguna</SelectItem>
                                <SelectItem value="Básica">Básica</SelectItem>
                                <SelectItem value="Intermedia">Intermedia</SelectItem>
                                <SelectItem value="Avanzada">Avanzada</SelectItem>
                            </SelectContent>
                            </Select>
                        )}
                    />
                </div>

              <div>
                <Label htmlFor="cameraConfidence">Confianza frente a cámara (1=Poca, 5=Mucha)</Label>
                <Controller
                    name="cameraConfidence"
                    control={control}
                    render={({ field: { value, onChange } }) => (
                        <div className="flex items-center gap-4 mt-2">
                            <span className="text-sm text-muted-foreground" aria-hidden="true">1</span>
                            <Slider
                                id="cameraConfidence"
                                defaultValue={[3]}
                                value={[value]}
                                onValueChange={(vals) => onChange(vals[0])}
                                max={5}
                                min={1}
                                step={1}
                                aria-label="Confianza frente a cámara"
                            />
                            <span className="text-sm text-muted-foreground" aria-hidden="true">5</span>
                        </div>
                    )}
                />
              </div>

               <div>
                  <Label>Estilos musicales con los que te identificas</Label>
                   <div className="mt-2 grid grid-cols-2 sm:grid-cols-3 gap-4">
                        {musicStyleOptions.map(style => (
                           <Controller
                                key={style}
                                name="musicStyles"
                                control={control}
                                render={({ field }) => (
                                    <div className="flex items-center gap-2">
                                        <Checkbox 
                                            id={`style-${style}`} 
                                            checked={field.value?.includes(style)}
                                            onCheckedChange={(checked) => {
                                                return checked
                                                    ? field.onChange([...(field.value || []), style])
                                                    : field.onChange(field.value?.filter(v => v !== style))
                                            }}
                                        />
                                        <Label htmlFor={`style-${style}`} className="font-normal">{style}</Label>
                                    </div>
                                )}
                            />
                        ))}
                   </div>
              </div>
          </div>
      </div>
      
      <div className="space-y-6 border-b border-border pb-12">
          <h2 className="text-xl font-headline font-semibold text-foreground">Disponibilidad y Logística</h2>
          <div className="grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
               <div className="sm:col-span-full space-y-4">
                    <fieldset>
                        <legend className="text-sm font-medium text-foreground">¿Disponibilidad para ensayos?</legend>
                        <Controller
                            name="rehearsalAvailability"
                            control={control}
                            render={({ field }) => (
                                <div className="flex items-center gap-2 mt-2">
                                    <Checkbox id="rehearsalAvailability" checked={field.value} onCheckedChange={field.onChange} aria-required="true" />
                                    <Label htmlFor="rehearsalAvailability" className="font-normal">Sí, tengo disponibilidad</Label>
                                </div>
                            )}
                        />
                         {errors.rehearsalAvailability && <p className="text-destructive text-sm mt-1">{errors.rehearsalAvailability.message}</p>}
                    </fieldset>

                    <fieldset>
                        <legend className="text-sm font-medium text-foreground">¿Puedes desplazarte al lugar de filmación por tus medios?</legend>
                         <Controller
                            name="canTravel"
                            control={control}
                            render={({ field }) => (
                                <RadioGroup onValueChange={field.onChange} defaultValue={field.value} className="mt-2 flex gap-4" aria-required="true">
                                    <div className="flex items-center space-x-2">
                                        <RadioGroupItem value="Sí" id="travel-yes" />
                                        <Label htmlFor="travel-yes" className="font-normal">Sí</Label>
                                    </div>
                                    <div className="flex items-center space-x-2">
                                        <RadioGroupItem value="No" id="travel-no" />
                                        <Label htmlFor="travel-no" className="font-normal">No</Label>
                                    </div>
                                </RadioGroup>
                            )}
                            />
                        {errors.canTravel && <p className="text-destructive text-sm mt-1">{errors.canTravel.message}</p>}
                    </fieldset>
               </div>
                <div className="col-span-full">
                    <Label htmlFor="medicalRestrictions">Restricciones médicas (opcional)</Label>
                    <Textarea id="medicalRestrictions" {...register("medicalRestrictions")} className="mt-1" rows={3} placeholder="Alergias, lesiones, medicamentos, etc."/>
                </div>
          </div>
      </div>

      <div className="space-y-6 border-b border-border pb-12">
            <h2 className="text-xl font-headline font-semibold text-foreground">Material Visual</h2>
            <div className="grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                <div className="sm:col-span-3">
                    <Label htmlFor="headshot">Foto de rostro (vertical)</Label>
                    <Input id="headshot" type="file" {...register("headshot")} accept="image/jpeg,image/png,image/webp" className="mt-1" aria-required="true" aria-invalid={!!errors.headshot} aria-describedby="headshot-error" />
                    <p className="text-muted-foreground text-sm mt-1">JPG, PNG, WEBP. Máx 5 MB.</p>
                    {errors.headshot && <p id="headshot-error" className="text-destructive text-sm mt-1">{errors.headshot.message as string}</p>}
                </div>
                <div className="sm:col-span-3">
                    <Label htmlFor="fullBodyPhoto">Foto de cuerpo completo (vertical)</Label>
                    <Input id="fullBodyPhoto" type="file" {...register("fullBodyPhoto")} accept="image/jpeg,image/png,image/webp" className="mt-1" aria-required="true" aria-invalid={!!errors.fullBodyPhoto} aria-describedby="fullBodyPhoto-error" />
                     <p className="text-muted-foreground text-sm mt-1">JPG, PNG, WEBP. Máx 5 MB.</p>
                    {errors.fullBodyPhoto && <p id="fullBodyPhoto-error" className="text-destructive text-sm mt-1">{errors.fullBodyPhoto.message as string}</p>}
                </div>
                 <div className="col-span-full">
                    <Label htmlFor="additionalPhotos">Fotos adicionales (hasta 5)</Label>
                    <Input id="additionalPhotos" type="file" {...register("additionalPhotos")} accept="image/jpeg,image/png,image/webp" multiple className="mt-1" aria-invalid={!!errors.additionalPhotos} aria-describedby="additionalPhotos-error" />
                     {errors.additionalPhotos && <p id="additionalPhotos-error" className="text-destructive text-sm mt-1">{errors.additionalPhotos.message as string}</p>}
                </div>
                 <div className="col-span-full">
                    <Label htmlFor="reelOrDanceLink">Enlace a reel</Label>
                    <Input id="reelOrDanceLink" type="url" {...register("reelOrDanceLink")} className="mt-1" placeholder="https://youtube.com/..." aria-describedby="reel-tip reelOrDanceLink-error" />
                    {danceSkills.length > 0 && <p id="reel-tip" className="text-primary text-sm mt-1">Consejo: Compartir un demo ayudará a tu aplicación.</p>}
                    {errors.reelOrDanceLink && <p id="reelOrDanceLink-error" className="text-destructive text-sm mt-1">{errors.reelOrDanceLink.message}</p>}
                </div>
            </div>
        </div>

      <div className="space-y-6">
        <h2 className="text-xl font-headline font-semibold text-foreground">Autorizaciones</h2>
         <div className="space-y-4">
            <div className="flex items-start gap-3">
                <Controller
                    name="socialConsent"
                    control={control}
                    render={({ field }) => <Checkbox id="socialConsent" checked={!!field.value} onCheckedChange={field.onChange} />}
                />
                <Label htmlFor="socialConsent" className="font-normal text-muted-foreground">Autorizo el uso de mi imagen en contenido de casting o redes sociales de la producción.</Label>
            </div>
            <div className="flex items-start gap-3">
                 <Controller
                    name="privacyConsent"
                    control={control}
                    render={({ field }) => <Checkbox id="privacyConsent" checked={!!field.value} onCheckedChange={field.onChange} aria-required="true" />}
                />
                <div className="flex-1">
                    <Label htmlFor="privacyConsent" className="font-normal text-muted-foreground">
                        Acepto el tratamiento de mis datos conforme a la{' '}
                         <Dialog>
                            <DialogTrigger asChild>
                                <button type="button" className="text-primary underline">Política de Privacidad</button>
                            </DialogTrigger>
                            <DialogContent className="sm:max-w-2xl">
                                <DialogHeader>
                                    <DialogTitle>Política de Privacidad y Tratamiento de Datos</DialogTitle>
                                    <DialogDescription>
                                        Última actualización: 25 de Agosto de 2025
                                    </DialogDescription>
                                </DialogHeader>
                                <div className="prose prose-sm dark:prose-invert max-h-[60vh] overflow-y-auto pr-4">
                                    <p>Al enviar este formulario, usted entiende y acepta los siguientes términos sobre el tratamiento de sus datos personales por parte de Videame (en adelante, "la Producción").</p>
                                    
                                    <h3 className="text-foreground">1. Datos Recopilados</h3>
                                    <p>La Producción recopilará los datos personales que usted proporcione en este formulario, incluyendo pero no limitado a: nombre completo, edad, datos de contacto, características físicas, experiencia, y material audiovisual (fotos y videos).</p>

                                    <h3 className="text-foreground">2. Finalidad del Tratamiento</h3>
                                    <p>Sus datos serán utilizados exclusivamente para los siguientes fines:</p>
                                    <ul className="list-disc pl-5">
                                        <li>Evaluar su perfil para oportunidades de casting en producciones audiovisuales actuales y futuras de la Producción.</li>
                                        <li>Contactarle para coordinar audiciones, ensayos o participaciones.</li>
                                        <li>Gestionar su participación en caso de ser seleccionado/a.</li>
                                    </ul>

                                    <h3 className="text-foreground">3. Almacenamiento y Seguridad</h3>
                                    <p>Su información será almacenada de forma segura en bases de datos gestionadas a través de servicios en la nube (Firebase de Google). Se han implementado medidas técnicas y organizativas para proteger sus datos contra el acceso no autorizado, alteración o destrucción.</p>
                                    
                                    <h3 className="text-foreground">4. Consentimiento de Imagen</h3>
                                    <p>Si marca la casilla de consentimiento para uso de imagen, autoriza a la Producción a utilizar su imagen, nombre y/o voz, capturados durante el proceso de casting o en el material que usted proporciona, para fines de promoción interna o en redes sociales relacionadas con el casting o la productora, sin derecho a compensación adicional.</p>

                                    <h3 className="text-foreground">5. Cesión de Datos</h3>
                                    <p>Sus datos no serán cedidos a terceros sin su consentimiento explícito, salvo que sea estrictamente necesario para la realización de la producción (e.g., directores, clientes finales del proyecto) o por obligación legal.</p>

                                    <h3 className="text-foreground">6. Conservación de Datos</h3>
                                    <p>Sus datos serán conservados en nuestra base de datos por un período de 5 años para futuras oportunidades de casting. Transcurrido este tiempo, serán eliminados de forma segura.</p>

                                    <h3 className="text-foreground">7. Sus Derechos</h3>
                                    <p>Usted tiene derecho a acceder, rectificar, cancelar u oponerse al tratamiento de sus datos. Para ejercer estos derechos, puede enviar un correo a [email de contacto de Videame].</p>

                                    <p className="font-bold">Al marcar la casilla "Acepto", usted confirma que ha leído, entendido y aceptado los términos de esta política de privacidad, liberando a Videame de cualquier reclamación futura relacionada con el tratamiento de sus datos para los fines aquí descritos.</p>
                                </div>
                                <div className="flex justify-end pt-4">
                                     <DialogClose asChild>
                                        <Button type="button">Cerrar</Button>
                                     </DialogClose>
                                </div>
                            </DialogContent>
                        </Dialog>
                        .
                    </Label>
                </div>
            </div>
             {errors.privacyConsent && <p className="text-destructive text-sm mt-1">{errors.privacyConsent.message}</p>}
            <div className="flex items-start gap-3">
                <Controller
                    name="over18"
                    control={control}
                    render={({ field }) => <Checkbox id="over18" checked={!!field.value} onCheckedChange={field.onChange} aria-required="true" />}
                />
                <Label htmlFor="over18" className="font-normal text-muted-foreground">Declaro ser mayor de 18 años.</Label>
            </div>
            {errors.over18 && <p className="text-destructive text-sm mt-1">{errors.over18.message}</p>}
        </div>
      </div>

      <div className="mt-12 flex items-center justify-end gap-x-6">
        <Button type="button" variant="ghost" onClick={() => reset()}>Cancelar</Button>
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? <><Loader2 className="mr-2 h-4 w-4 animate-spin" />Enviando...</> : 'Enviar Postulación'}
        </Button>
      </div>
    </form>
  );
}

    